
@import "~animate.css/animate.css";


.App {
  text-align: center;
}

.pdt-mob
{
 flex:none;
}
.flex-desk
{
  width:60vw;
  overflow-x:scroll;
}

.features-wrap
{
  width:50vw;
}

.btn-contact-mobile
{
visibility:hidden;
}

   ::-webkit-scrollbar{
        height: 10px;
        width: 10px;
        background: white;
    }
    ::-webkit-scrollbar-thumb:vertical{
        width:7px;
        background: grey;
        border-radius: 10px;
    }
    ::-webkit-scrollbar-thumb:horizontal{
        width:20px;
        height:10px;
        background: grey;
        border-radius: 10px;
    }
    ::-webkit-scrollbar-track {
  border-radius: 0;
  background: #eeeeee;
}

.progress-bar {
  position: fixed;
  top: 10vh;
  left: 0;
  right: 0;
  height: 10px;
  background-image: linear-gradient( 109.6deg, rgba(156,252,248,1) 11.2%, rgba(110,123,251,1) 91.1% );
  transform-origin: 0%;
  z-index:100;
  border:1px solid black;
}

.load-main
{
height:100%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.loading{
  align-items:center;
  margin-left:41vw;
  margin-top:25vh;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media screen and (max-width: 750px) {
.loading{
  margin-left:25vw;

}

}


@media screen and (max-width: 900px) {
.loading{
  margin-left:30vw;

}
.btn-contact-mobile
{
visibility:visible;
position:fixed;
bottom:0;
right:0;
width:30vw;
height:8vh;
margin-top:10px;
}

.progress-bar {
   visibility:hidden;
}

.blog-img
{
  display:flex;
  align-items:center;
  justify-content:center;

}

.contact-mob
{  
   position:fixed;
   bottom:20vh;
   left:4vh;
   z-index:100;
}


.pdt-mob
{
 flex:1;
}

.box-main
{
   display:flex;
   justify-content:center;
   background-color:black;
}
.features-wrap
{
  width:90vw;
}


.flx-mob
{
  width:90vw;
}



.slider__control
{
  visibility:hidden;
 }


}
