.listItem-wrap img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.card-info
{
 transition: transform 100ms ease-in;
  width:20vw;
}

.card-info:hover {
  transform: scale(1.07);
}
  

.listItem-wrap {
  border: 2px solid var(--offblack) ;
  border-radius: 12px;
  margin: 6px;
  background-color: var(--offWhite);
}

.name-header
{
  font-display:inherit;
  margin-top:4px;
}

.listItem-header
{
  text-align: center;
}

.listItem-wrap-body{
  padding: 9px;
  background-color: var(--offWhite);
  border-bottom-left-radius: 13px;
  border-bottom-right-radius: 13px;
}
.listItem-wrap header span {
  font-size: 0.7rem;
  background-color: #97ffff;
  padding: 0.5rem;
  border-radius: 10px;
}
.listItem-wrap footer {
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.listItem-wrap footer p:first-child span {
  color: rgba(0, 0, 0, 0.26);
  font-weight: 600;
}

.details{
display: flex;

}

.details-main{
  display: flex;
  flex-direction: column;
}

.details-span-0{
  margin-right: 92px;
}

.details-span-1{
  margin-right: 95px;
}

.details-span-2{
  margin-right: 45px;
  
}

.details-span-3{
 margin-right: 90px;
}
.details-span-4
{
  margin-right:35px;
  }


.btn-info-custom
{
  display: block;
}

.Modal-main{
  margin-left: 5px;
  display: flex;
  width: 98vw;
  height: 100vh;
  background-color: var(--offWhite);
  border-radius: 12px;
  
}


.details-primary-wrap
{
  overflow-y: scroll;
  width: 40vw;
  padding: 16px;
  margin-left: 4px;
}

/* Modal CSS */

.Image-modal
{
  margin: 5px;
  width: 500px;
  height: 70vh;
}




.det-tag-wrap{
margin-top:12px;
margin-bottom:8px;

}


.details-primary-body-wrap
{
border:1px solid white;
box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2), 0 0 20px 0 rgba(0, 0, 0, 0.19);
border-radius:9px;
margin-bottom:40px;
padding:9px;
}


.additional-details-wrap,.addr-details
{
  
border:1px solid white;
box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2), 0 0 20px 0 rgba(0, 0, 0, 0.19);
border-radius:9px;
margin-bottom:40px;
padding:9px;
}



.addr-det ,.additonal-details
{display:flex;}


.contact-form,.image-desc
{
  border:1px solid white;
box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2), 0 0 20px 0 rgba(0, 0, 0, 0.19);
border-radius:9px;
}


.contact-header-desc
{
  margin-top:25px;
}


.btn-back-custom
{
height:100vh;
}


@media screen and (max-width: 980px) {
.card-info
{
 transition: transform 100ms ease-in;
  width:40vw;
}

}


@media screen and (max-width: 700px) {
.card-info
{
 transition: transform 100ms ease-in;
  width:40vw;
}
  .card-info
{
 width:70vw;
}



}


