.search-page{
    margin: 0 20px;
}
.search-page-info {
    padding: 20px;
}

.search-page-info > p {
    margin-bottom: 10px;
    font-size: 14px;
}

.search-page-info > h1 {
    margin-bottom: 30px;
}

.search-page-info > button {
    border-radius: 30px;
    text-transform: inherit;
    margin: 5px;
}

.icon-filter-mobile
{
    visibility:hidden;
    
}


.bar{
  width:800px;
  background:white;
  box-shadow:0 0 5px hsl(0 0% 78%);
  height:55px;
  border-radius:12px;
  display:flex;
  justify-content:center;
  font-size:0.6rem;
  margin-left:20vw
}

.bar div{
  border-radius:inherit;
  padding:0.8rem 1.5rem;
  transition:background 250ms ease;
}

.bar div:hover{
  background:hsl(0 0% 94%);
}

.location{
  width:34%;
}
.check-in, .check-out, .guests{
  width:22%;
}

input[type="text"]{
  background:none;
  border:none;
  padding:0.2rem 0 0 0;
}

input[type="text"]:focus{
  outline:none;
}

::placeholder{
  font-size:0.75rem;
}

.guests{
  position: relative;  
}
.guests span{
  position:absolute;
  top:50%;
  right:10px;
  transform:translateY(-50%);
  background:#FF385C;
  color:white;
  font-size:0.8rem;
  padding:0.7rem;
  border-radius:50%;
}

.bar>div{
  position:relative;
}


.bar>div::before{
  position:absolute;
  content:"";
  left:0;
  top:50%;
  transform:translateY(-50%);
  width:1px;
  height:55%;
  background:hsl(0 0% 90%);
}

.bar>div:nth-of-type(1)::before{
   background:transparent;
}


.bar>div:hover::before{
  background:transparent;
}



.togglebtn
{
border:#FFF;

}

 .root {
    width: '100%';
  }
  .thumb {
    color:linear-gradient(90deg,#20BF55,#01BAEF,100%);
  }
  .rail {
    color: rgba(0, 0, 0, 0.26);
  }
  .track {
    color:linear-gradient(90deg,#009FFD,#01BAEF,100%);
  }


.details-listing
{
display:flex;
}


.list
{
   
   overflow-y:scroll;
   height:80vh;
}
.map
{
 margin-top:20px;
 border-radius:5px;
 }




@media screen and (max-width: 700px) {
.map{
display:none;
}

.bar
{
  display:none;
  width:80vw;
  height:30vh;
  transition:background 250ms ease;
  position:absolute;
  z-index:200;
  margin-top:5px;
  margin-left:10px;
}

.location
{
  margin-left:10px;
}


.icon-filter-mobile
{
    visibility:visible;
    margin-left:-80vw;
}

}





