
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background: #fff;
    z-index: 100;
    width: 100vw;
    top: 0;
}


@media screen and (max-width: 700px) {
    
    .header_center > input{
        width: 10%;
        padding-left: 80px;
        margin-right: 5px;
    } 

    .header_right  {
        padding-left: 5px;
    }
    
    .header_right > p {
        display: none;
    }
    
    .header-logo{
       display:none;
    }
   .nav-custom
   {
      display:flex;
      flex-direction:row;
   }

}
