.list-wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  padding-top: 20px;
display: flex;
padding-bottom: 30px;
font-size: 20px;

}


.home_list-wrap
{
    align-items:center;
    margin-left:70px;
    display:flex;
    overflow-x:scroll;
}

.list-wrap-true{
 display: flex;
 padding: 1rem;
 justify-content: space-between;
}


.label-location{
  width: 33vw;
  margin-top: 15px;
  margin-left:25px;
  background-color:white;
  z-index:2;
}


@media screen and (max-width: 700px) {
  
.home_list-wrap
{
    align-items:center;
    margin-left:20px;
    display:flex;
    overflow-x:scroll;
}

}
